import React, { lazy, Suspense } from "react";
import { Dialog, Button, EyeOpenIcon, Pill } from 'evergreen-ui';

import skills from './SkillsStore';
import Spinner from '../common/Spinner';
// import SkillsGrid from './Skills';


const SkillsGrid = lazy(() => import("./Skills"))


class SkillPane extends React.Component {

  state = {
    isShown: false
  }

  togglePane = (isShown) => this.setState({ isShown });

  render() {
    return (
    <React.Fragment>
      <Dialog
        isShown={this.state.isShown}
        title="My Skills"
        onCloseComplete={() => this.togglePane(false)}
        hasFooter={false}
        height="80%"
        width="75%"
      >
        <Suspense fallback={<Spinner />}>
          <SkillsGrid />
        </Suspense>

      </Dialog>
      <Button
        style={{color: "black"}}
        height={32}
        intent="none"
        iconBefore={EyeOpenIcon}
        onClick={() => this.togglePane(true)}
        margin={8}
      >
        Skills <Pill display="inline-flex" marginLeft={8}>{skills.length}</Pill>
      </Button>
    </React.Fragment>
  )};
}

export default SkillPane