import React, { lazy, Suspense } from "react";
import { Dialog, Button, InfoSignIcon } from 'evergreen-ui';

import Spinner from '../common/Spinner';


const SummaryInfo = lazy(() => import("./SummaryInfo"))


class Summary extends React.Component {

  state = {
    isShown: false
  }

  togglePane = (isShown) => this.setState({ isShown });

  render() {
    return (
    <React.Fragment>
      <Dialog
        isShown={this.state.isShown}
        title="Summary"
        onCloseComplete={() => this.togglePane(false)}
        hasFooter={false}
        height="80%"
        width="75%"
      >
        <Suspense fallback={<Spinner />}>
          <SummaryInfo />
        </Suspense>
      </Dialog>
      <Button
        style={{color: "black"}}
        height={32}
        intent="none"
        iconBefore={InfoSignIcon}
        onClick={() => this.togglePane(true)}
        margin={8}
      >
        Summary
      </Button>
    </React.Fragment>
  )};
}

export default Summary